import React, { useState, useEffect } from "react";

import Container from "@mui/material/Container";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";

import data from "./Data/out.json";
import DisplayComparison from "./Data/Components/DisplayComparison";

import Button from "@mui/material/Button";

function App() {
  const numComparisons = data.length;
  const [datumNumber, setDatumNumber] = useState(0);
  const [datum, setDatum] = useState(data[datumNumber]);

  useEffect(() => {
    setDatum(data[datumNumber]);
  }, [datumNumber]);

  const handlePrevClick = () => {
    setDatumNumber(datumNumber - 1);
  };

  const handleNextClick = () => {
    setDatumNumber(datumNumber + 1);
  };

  const renderPrevButton = () => {
    return (
      <Button variant="text" onClick={handlePrevClick}>
        Previous Comparison
      </Button>
    );
  };

  const renderNextButton = () => {
    return (
      <Button variant="text" onClick={handleNextClick}>
        Next Comparison
      </Button>
    );
  };

  return (
    <Container>
      <Grid container spacing={2}>
        <Grid item xs={6}>
          {datumNumber > 0 ? renderPrevButton() : null}
        </Grid>

        <Grid item xs={6}>
          <Box display="flex" justifyContent="flex-end">
            {datumNumber < numComparisons - 1 ? renderNextButton() : null}
          </Box>
        </Grid>
      </Grid>

      <Box sx={{ flexGrow: 1, m: 1 }}>
        <DisplayComparison
          datum={datum}
          datumNumber={datumNumber}
        ></DisplayComparison>
      </Box>
    </Container>
  );
}

export default App;
